import React, {useState} from 'react';
import {Link, useLocation} from 'react-router-dom';
import {Controller, useForm} from 'react-hook-form';
import {toast} from 'react-toastify';
import {
    Button,
    Container,
    Row,
    Col,
    Form,
    FormGroup,
    Label,
    Input,
    Card,
    CardBody,
    Nav,
    NavItem,
    NavLink,
    TabContent,
    TabPane,
    Alert
} from 'reactstrap';
import classnames from 'classnames';
import {LogIn} from 'lucide-react';
import {useAuthStateManager} from '../../hooks/useAuthStateManager';
import SocialLogins from '../social_logins/SocialLogins';
import DisplayFormError from '../utility/DisplayFormError';
import {
    AuthTokens,
    LoginWithEmailModel,
    ResendEmailConfirmationEmailCommand
} from '../../types';
import useResendEmailConfirmationEmail from '../../api/auth/useResendEmailConfirmationEmail';
import {useLoginWithEmail} from '../../api/auth/useLoginWithEmail';
import unknownErrorToString from '../../lib/unknownErrorToString';
import ErrorDisplay from '../utility/ErrorDisplay';
import useLocalStorage from '../../hooks/useLocalStorage';
import CheckFromEmailSpamJunkWarning from '../utility/CheckFromEmailSpamJunkWarning';
import useAppSettings from '../../hooks/useAppSettings';
import {
    PasswordLoginRequest,
    usePasswordLogin
} from '../../api/auth/useAuthHooks';
import Certavue from '../utility/Certavue';

const LoginPage = () => {
    const {onAuthenticated, redirectOnAuthenticated, removeSignInRedirect} =
        useAuthStateManager();
    const {state} = useLocation();
    const [apiError, setApiError] = useState<unknown>(null);
    const [awaitingResponse, setAwaitingResponse] = useState(false);
    const [activeTab, setActiveTab] = useLocalStorage('LoginTab', 'password');
    const {auth} = useAppSettings();

    const hasSocialLogins = auth.microsoft.enabled || auth.google.enabled;

    const {
        control,
        handleSubmit,
        formState: {errors},
        clearErrors
    } = useForm<LoginWithEmailModel>({
        defaultValues: {email: ''},
        mode: 'onChange'
    });

    const [passwordLoginRequest, setPasswordLoginRequest] =
        useState<PasswordLoginRequest>({
            username: '',
            password: ''
        });

    const passwordLoginMutation = usePasswordLogin();
    const resendEmailConfirmationEmailMutation =
        useResendEmailConfirmationEmail({
            onSuccess: () => {
                toast.success('Confirmation email has been resent.');
            },
            onError: (error: unknown) => {
                setApiError(unknownErrorToString(error));
            }
        });

    const loginWithEmailMutation = useLoginWithEmail({
        onSuccess: () => {
            removeSignInRedirect();
            toast.success('Please check your email for a login link.');
        },
        onError: (error: unknown) => {
            setApiError(unknownErrorToString(error));
        }
    });

    const onSubmitSuccess = (authResponse: AuthTokens) => {
        onAuthenticated(authResponse);
        redirectOnAuthenticated((state as {redir?: string | null})?.redir);
    };

    const onPasswordFormSubmit = async (event: React.FormEvent) => {
        event.preventDefault();
        setAwaitingResponse(true);
        try {
            const result =
                await passwordLoginMutation.mutateAsync(passwordLoginRequest);
            onSubmitSuccess(result);
        } catch (error: unknown) {
            setApiError(error);
        } finally {
            setAwaitingResponse(false);
        }
    };

    const onEmailFormSubmit = (data: LoginWithEmailModel) => {
        setApiError(null);
        clearErrors();

        const redirectUrl = removeSignInRedirect();

        const loginWithEmail: LoginWithEmailModel = {
            email: data.email,
            redir: redirectUrl
        };

        loginWithEmailMutation.mutate(loginWithEmail);
    };

    const toggleTab = (tab: string) => {
        if (activeTab !== tab) {
            setActiveTab(tab);
            setApiError(null);
            clearErrors();
        }
    };

    const resendEmailConfirmationEmail = async () => {
        setAwaitingResponse(true);
        try {
            const command: ResendEmailConfirmationEmailCommand = {
                email: passwordLoginRequest.username
            };
            await resendEmailConfirmationEmailMutation.mutateAsync(command);
        } catch (error: unknown) {
            setApiError(error);
        } finally {
            setAwaitingResponse(false);
        }
    };

    // Determine if the error is due to unconfirmed email
    const isUnconfirmedEmail =
        ((apiError as {problemDetails?: {authError?: {error?: string}}})
            ?.problemDetails?.authError?.error ?? '') === 'unconfirmed_email';

    return (
        <Container className="my-5">
            <Row className="justify-content-center">
                <Col xs={12} md={8} lg={6}>
                    <div className="text-center mb-4">
                        <Certavue />
                        <h2 className="mt-2">Welcome Back!</h2>
                        <p className="text-muted">
                            Log in to continue to certavue.com
                        </p>
                    </div>
                    <Card>
                        <CardBody>
                            <Nav tabs className="justify-content-center mb-4">
                                <NavItem>
                                    <NavLink
                                        className={classnames({
                                            active: activeTab === 'password'
                                        })}
                                        onClick={() => toggleTab('password')}
                                    >
                                        Password Login
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink
                                        className={classnames({
                                            active: activeTab === 'email'
                                        })}
                                        onClick={() => toggleTab('email')}
                                    >
                                        Email Login
                                    </NavLink>
                                </NavItem>
                            </Nav>
                            <TabContent activeTab={activeTab}>
                                <TabPane tabId="password">
                                    <Form onSubmit={onPasswordFormSubmit}>
                                        <FormGroup>
                                            <Label for="username">Email</Label>
                                            <Input
                                                type="text"
                                                id="username"
                                                placeholder="Enter your email"
                                                value={
                                                    passwordLoginRequest.username
                                                }
                                                onChange={e =>
                                                    setPasswordLoginRequest({
                                                        ...passwordLoginRequest,
                                                        username: e.target.value
                                                    })
                                                }
                                            />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for="password">
                                                Password
                                            </Label>
                                            <Input
                                                type="password"
                                                id="password"
                                                placeholder="Enter your password"
                                                value={
                                                    passwordLoginRequest.password
                                                }
                                                onChange={e =>
                                                    setPasswordLoginRequest({
                                                        ...passwordLoginRequest,
                                                        password: e.target.value
                                                    })
                                                }
                                            />
                                        </FormGroup>
                                        <ErrorDisplay error={apiError} />
                                        {isUnconfirmedEmail && (
                                            <Alert color="warning">
                                                <p className="mb-2">
                                                    Your email address is not
                                                    confirmed. Please confirm
                                                    your email to proceed.
                                                </p>
                                                <Button
                                                    color="warning"
                                                    onClick={
                                                        resendEmailConfirmationEmail
                                                    }
                                                    disabled={awaitingResponse}
                                                >
                                                    Resend Confirmation Email
                                                </Button>
                                            </Alert>
                                        )}
                                        <div className="d-flex justify-content-between align-items-center">
                                            <Link
                                                to="/forgot-password"
                                                className="text-muted"
                                            >
                                                Forgot password?
                                            </Link>
                                            <Button
                                                color="primary"
                                                type="submit"
                                                disabled={awaitingResponse}
                                            >
                                                <LogIn
                                                    size={16}
                                                    className="me-2"
                                                />
                                                Log In
                                            </Button>
                                        </div>
                                    </Form>
                                </TabPane>
                                <TabPane tabId="email">
                                    <Form
                                        onSubmit={handleSubmit(
                                            onEmailFormSubmit
                                        )}
                                    >
                                        <FormGroup>
                                            <Label for="email">Email</Label>
                                            <Controller
                                                control={control}
                                                name="email"
                                                rules={{
                                                    required:
                                                        'Email is required',
                                                    pattern: {
                                                        value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                                                        message:
                                                            'Invalid email address'
                                                    }
                                                }}
                                                render={({field}) => (
                                                    <Input
                                                        type="email"
                                                        id="email"
                                                        placeholder="Enter your email"
                                                        {...field}
                                                    />
                                                )}
                                            />
                                            <DisplayFormError
                                                error={errors.email}
                                                fieldDisplayName="Email"
                                            />
                                        </FormGroup>
                                        <CheckFromEmailSpamJunkWarning />
                                        <ErrorDisplay error={apiError} />
                                        <div className="text-end">
                                            <Button
                                                color="primary"
                                                type="submit"
                                                disabled={awaitingResponse}
                                            >
                                                Send Login Link
                                            </Button>
                                        </div>
                                    </Form>
                                </TabPane>
                            </TabContent>
                            {hasSocialLogins && (
                                <div className="text-center mt-4">
                                    <p className="text-muted">Or log in with</p>
                                    <SocialLogins registration={false} />
                                </div>
                            )}
                        </CardBody>
                    </Card>
                    <div className="text-center mt-4">
                        <p className="text-muted">
                            Don't have an account?{' '}
                            <Link to="/sign-up">Create one now</Link>
                        </p>
                    </div>
                </Col>
                <Col xs={12} md={8} lg={6}>
                    <h3 className="text-lavender mb-3">Need something else?</h3>

                    <div className="mb-4">
                        <h4 className="text-mulled-wine">New to certavue?</h4>
                        <p>
                            If your company already uses certavue and you donâ€™t
                            yet have your own login, quickly{' '}
                            <Link
                                to="/sign-up"
                                className="text-primary font-weight-bold"
                            >
                                Sign up here
                            </Link>{' '}
                            to get started.
                        </p>
                    </div>

                    <div>
                        <h4 className="text-mulled-wine">
                            Want to bring certavue to your company?
                        </h4>
                        <p>
                            Easily{' '}
                            <Link
                                to="/register-company"
                                className="text-primary font-weight-bold"
                            >
                                Register your Company
                            </Link>{' '}
                            to begin simplifying your organization's
                            transformation management today.
                        </p>
                    </div>
                </Col>
            </Row>
        </Container>
    );
};

export default LoginPage;
