import {ResendEmailConfirmationEmailCommand, EmptyResult} from '../../types';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';
import {
    useMutation,
    UseMutationResult,
    UseMutationOptions
} from '@tanstack/react-query';

const useResendEmailConfirmationEmail = (
    options?: UseMutationOptions<
        EmptyResult,
        Error,
        ResendEmailConfirmationEmailCommand
    >
): UseMutationResult<
    EmptyResult,
    Error,
    ResendEmailConfirmationEmailCommand
> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();

    return useMutation({
        mutationFn: async (command: ResendEmailConfirmationEmailCommand) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/auth/resend-email-confirmation-email`,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(command)
                }
            );
            return handleResponse(response);
        },
        ...options
    });
};

export default useResendEmailConfirmationEmail;
