import {useMutation, UseMutationResult} from '@tanstack/react-query';
import {AuthTokens} from '../../types';
import {handleResponse} from '../handleResponse';
import baseUrl from '../baseUrl';
import ProblemDetailsError from '../ProblemDetailsError';

export type PasswordLoginRequest = {
    username: string;
    password: string;
};

const makeTokenRequest = async (formData: FormData): Promise<AuthTokens> => {
    const response = await fetch(`${baseUrl}/api/auth/tokens`, {
        method: 'POST',
        body: formData
    });

    return handleResponse<AuthTokens>(response);
};

const loginWithPassword = async (
    credentials: PasswordLoginRequest
): Promise<AuthTokens> => {
    const formData = new FormData();
    formData.append('grant_type', 'password');
    formData.append('username', credentials.username);
    formData.append('password', credentials.password);

    return makeTokenRequest(formData);
};

export function usePasswordLogin(): UseMutationResult<AuthTokens, Error | ProblemDetailsError, PasswordLoginRequest> {
    return useMutation({
        mutationFn: loginWithPassword,
        retry: 0 // No retries for login attempts
    });
}
