import {LoginWithEmailModel} from '../../types';
import ProblemDetailsError from '../ProblemDetailsError';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';
import {
    useMutation,
    UseMutationResult,
    UseMutationOptions
} from '@tanstack/react-query';

export const useLoginWithEmail = (
    options?: UseMutationOptions<
        void,
        Error | ProblemDetailsError,
        LoginWithEmailModel
    >
): UseMutationResult<
    void,
    Error | ProblemDetailsError,
    LoginWithEmailModel
> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();

    return useMutation({
        mutationFn: async (loginWithEmail: LoginWithEmailModel) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/auth/login-with-email`,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(loginWithEmail)
                }
            );
            return handleResponse(response);
        },
        ...options
    });
};
